<script>
import {reactive} from 'vue'
import {mapState, mapGetters} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  setup(){
    
    let current = {};
    let feature = {};

    let vip = {
      "0": {
        name: "精英卡",
        price: "0",
        color: "#0FF",
        quota: [1, 2, "不", "不"],
      },
      "1": {
        name: "银卡",
        price: "100",
        color: "#CCC",
        quota: [3, 10, "不", "不"],
      },
      "2": {
        name: "金卡",
        price: "500",
        color: "#FF0",
        quota: [5, 30, "", "不"],
      },
      "3": {
        name: "白金卡",
        price: "1000",
        color: "#FFF",
        quota: [20, 100, "", "不"],
      },
      "4": {
        name: "钻石卡",
        price: "5000",
        color: "#0F0",
        quota: [100, 500, "", ""],
      },
    };

    let fun = [{
      icon: "font-o",
      name: "策略模板",
      value: " 个",
      desc: "可以根据不同的币种制定不同的量化策略",
    },{
      icon: "cash-back-record",
      name: "手动建仓",
      value: " 个",
      desc: "手动建仓买进选择好的币种，机器人根据设定的量化策略实时跟踪、自动交易；手动建仓和自动建仓共享同时在线任务额度",
    },{
      icon: "after-sale",
      name: "自动建仓",
      value: "支持",
      desc: "选择系统内置的建仓方案和设定好的量化策略，机器人自动建仓、自动交易",
    },{
      icon: "expand-o",
      name: "自定义自动建仓方案",
      value: "支持",
      desc: "设置不同指标组合，自定义建仓方案，机器人根据该方案和设定的量化策略自动建仓、自动交易",
    },];

    let show = false;
    let price = 0;

    const state = reactive({current, feature, vip, fun, show, price});
    return {state};
  },

  created() {
    this.state.current = this.state.vip[0];
    this.state.feature = this.state.fun;
  },

  mounted(){
    this.onChange(0);
  },

  computed: {
    bg(){
      return "url(" + require('../assets/vip.png') + ") no-repeat top center";
    },
    level(){
      if (!this.authorized)
        return "尚未登录";
      let l = this.profile.level || 0;
      return this.state.vip[l]["name"] + "会员";
    },
    intro(){
      if (!this.authorized)
        return "有效期登录后可见";
      return "有效期至 2021-10-31"
    },

    ...mapState(["profile"]),
    ...mapGetters(["authorized", "uid", "nickname", "balance"]),
  },

  methods: {
    onChange(i) {
      this.state.current = this.state.vip[i];
      let arr = [];
      for (let i in this.state.fun){
        let str = this.state.current.quota[i];
        let obj = {...{}, ...this.state.fun[i]};
        obj.value = str + obj.value;
        arr.push(obj);
      }
      this.state.feature = arr;
    },

    onBuy(){
      if (!this.authorized){
        this.$toast("请先登录");
        this.$router.push("/login");
        return;
      }
      this.state.show = true;
    },
    onPay(){
      console.log("余额：", this.profile.balance);
    },
  },
}
</script>
